import { Component } from '@angular/core';
import { PanelMenu, PanelMenuEvent, DropdownItemEnum } from './panel-menu/panel-menu.options';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  panelMenuOptions: PanelMenu;
  panelHeight: number;
  title: string;
  constructor() {
    this.title = 'Title';
    this.panelHeight = Math.floor(.90 * window.innerHeight);
    this.panelMenuOptions = {
      collapse: true,
      fullScreen: true,
      dropdown: [
        DropdownItemEnum.Columns,
        DropdownItemEnum.Csv,
        DropdownItemEnum.Filter,
        DropdownItemEnum.Reset
      ]
    }
  }
}
